import {http} from "./http.js"
export function getListTest(data) {
  return http({
    url: '/user/list',
    method: "GET",
    atType:"no",
	data
  })
}
export function addTest(data) {
  return http({
    url: '/user/add',
    method: "POST",
    atType:"no",
	notForm: true,
	data
  })
}

